footer {
    background-color: var(--lightgray);
    overflow: auto;
}

footer input {
    padding: 11px 10px;
    width: 300px;
    margin-right: 30px;
    margin-right: 2rem;
    border: 0;
    text-align: center;
}

footer .wrap {
    padding: 30px 15px;
    padding: 2rem 1rem;
}

.social-links a {
    font-size: 30px;
    font-size: 2rem;
    padding: 15px;
    padding: 1rem;
    margin-bottom: 30px;
    margin-bottom: 2rem;
    display: inline-block;
    color: #44545E;
}

.copyright {
    border-top: 2px solid #44545E;
    padding: 15px 0 3px;
    padding: 1rem 0 .2rem;
}

.contact-container {
    width: 100%;
    text-align: center;
    font-size: 15px;
}

.contact {
    margin: auto;
    width: auto;
    color: var(--darkgray);
    letter-spacing: 2px;
    padding: 8px 20px 8px 20px;
    border-width: 1px;
    border-color: var(--darkgray);
    border-radius: 2px;
    -moz-border-radius: 2px;
    -webkit-border-radius: 2px;
}