/* ----------------------------------------
Forms
------------------------------------------- */

.contact {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}

.contact button {
    width: 100%;
    margin-bottom: 2em;
}

textarea {
    width: 100%;
    display: block;
}

.contact-heading {
    margin-bottom: 45px;
    margin-bottom: 3rem;
    text-transform: none;
}

.input,
.textarea {
    border: 2px solid var(--darkgray);
    margin-bottom: 12px;
    margin-bottom: .8rem;
    padding: 10px 10px;
    outline: 0;
}

::-webkit-input-placeholder {
    color: var(--text);
    text-transform: uppercase;
    letter-spacing: 0.75px;
    letter-spacing: .05rem;
    font-size: 12.299px;
    font-size: .82rem;
    font-weight: 600;
}

:-moz-placeholder {
    /* Firefox 18- */
    color: var(--text);
    text-transform: uppercase;
    letter-spacing: 0.75px;
    letter-spacing: .05rem;
    font-size: 12.299px;
    font-size: .82rem;
    font-weight: 600;
}

::-moz-placeholder {
    /* Firefox 19+ */
    color: var(--text);
    text-transform: uppercase;
    letter-spacing: 0.75px;
    letter-spacing: .05rem;
    font-size: 12.299px;
    font-size: .82rem;
    font-weight: 600;
}

:-ms-input-placeholder {
    color: var(--text);
    text-transform: uppercase;
    letter-spacing: 0.75px;
    letter-spacing: .05rem;
    font-size: 12.299px;
    font-size: .82rem;
    font-weight: 600;
}